<template>
    <el-dialog :title="titleShow==true?'add':'edit'"  v-model="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="PayItemUid:" :label-width="formLabelWidth">
                <el-input v-model.trim="uid" auto-complete="off" size="small"></el-input>
            </el-form-item>
            <el-form-item label="EmpCode:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.EmpCode" auto-complete="off" size="small" :required="true"></el-input>

            </el-form-item>
            <el-form-item label="CompanyCode:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.CompanyCode" auto-complete="off" size="small" disabled></el-input>
            </el-form-item>
            <el-form-item label="Amount:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Description" auto-complete="off" size="medium"></el-input>
            </el-form-item>
            <el-form-item label="EffectiveDate:" :label-width="formLabelWidth">
                <el-date-picker
                        size="small"
                        v-model.trim="singleDate"
                        type="date"
                        placeholder="Select EffectiveDate">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="Function:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Function" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="FunctionName:" :label-width="formLabelWidth" :required="true">
                <el-input v-model.trim="form.FunctionName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="TemplateCode:" :label-width="formLabelWidth" :required="true">
                <el-input v-model.trim="form.TemplateCode" auto-complete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">Cancel</el-button>
            <el-button type="primary" @click="submit()">Submit</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
.updatePwd{
    .el-dialog__header{
        padding:30px;
        .el-dialog__headerbtn{
            width:50px;
            height:50px;
            margin:-30px -30px 0 0;
        }
    }
    .el-dialog__body{
        padding:30px 50px 0 0;
    }
}
</style>

<script>
import QuRequest from '../../../core/QuRequest'

const quRequest = new QuRequest()

export default {

    data() {
        return {
            type:[
                { 'name': 'Employee Contribution','value': 4},
                { 'name': 'deduction', 'value': 3},
                { 'name': 'Employer Contribution','value': 0},
                { 'name': 'Earning', 'value': 1},
            ],
            titleShow:false,
            dialogForm: false,
            formLabelWidth: '150px',
            disabled:true,
            isShow:true,
            uid:'',
            form:{
                'EmpCode':this.$route.query.EmpCode,
                'CompanyCode':this.$route.query.CompanyCode,
               // 'Description': '',
               // 'DisplayName':'',
                'Function':'',
                'FunctionName':'',
               // 'PayType':'',
                'TemplateCode':'',
                'Amount':'',
                'EffectiveDate':'',
            }
        }
    },
    computed:{
        singleDate: {
            set (newValue) {
                if(newValue){
                    this.form.EffectiveDate = this.formatDate(newValue)
                }else{
                    this.form.EffectiveDate = '';
                }
            },
            get () {
                return this.form.EffectiveDate
            }
        },
    },
    mounted() {
        this.getUid();
    },
    methods: {
        getUid(){
            quRequest.send(quRequest.Apis.qutuiba.member.uid
            ).then(res => {
                this.uid = res.result[0];
            }).catch(err => {
                this.$message.error(`Failed to retrieve data：${ err.message || 'Unknown error' }`);
            }).fin(() => {
            }).done();
        },

        formatDate(dateStr){
            let date = new Date(dateStr);
            let y = date.getFullYear();
            let m = date.getMonth() + 1;
            let d = date.getDate();
            return [y, this.addZero(m), this.addZero(d)].join('/');
        },
        addZero: function (num) {
            return (num > 9 ? '' : '0') + num
        },
        showDialog(item) {
            if(item){
                this.isShow = false;
                this.form.Amount= item.Amount;
                this.form.TemplateCode =item.TemplateCode;
                this.form.PayType=item.PayType;
                this.form.FunctionName =item.FunctionName;
                this.form.Function= item.Function;
                this.form.DisplayName =item.DisplayName;
                this.form.Description=item.Description;
                this.form.CompanyCode =item.CompanyCode;
                this.titleShow = false;
            }else{
                this.titleShow = true;
                this.form.Amount= '';
                this.form.TemplateCode ='';
                this.form.EffectiveDate='';
                this.form.FunctionName ='';
                this.form.Function= '';
                this.form.DisplayName ='';
                this.form.Description='';
                this.form.CompanyCode =this.$route.query.CompanyCode;
                this.form.EmpCode  = this.$route.query.EmpCode;

            }

            this.dialogForm = true;
        },
        submit(){
            quRequest.send(quRequest.Apis.qutuiba.member.update,this.titleShow==true?{'json' :'{update:{PayItem:['+JSON.stringify(Object.assign({'PayItemUid':this.uid},this.form))+']}}'}:{'json' :'{update:{PayItem:['+JSON.stringify(this.form)+']}}'}
            ).then(res => {
                this.$message.success("Submit Success！");
                this.dialogForm = false;
                this.$emit('update')
            }).catch(err => {
                this.$message.error(err.message);
            }).fin(() => {
                this.loading = false;
            }).done();
        }
    }
}
</script>


